import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginModule } from './auth/login/login.module';
import { LoginRedirect, EnsureAuthenticated } from './service/_guards';
import { IsCompanySelected } from './service/_guards/company-selected.service';
import { CompanyRedirectService } from './service/_guards/company-redirect.service';


const routes: Routes = [

  {
    path: '',
    redirectTo: '/dashboard',
    pathMatch: 'full'
  },

  {
    path: 'home',
    loadChildren: () => import('./auth/login/login.module').then(m => m.LoginModule),
    canActivate: [LoginRedirect],
  },
  {
    path: 'forgot-password',
    loadChildren: () => import('./auth/forgot-password/forgot-password.module').then(m => m.ForgotPasswordModule),
    canActivate: [LoginRedirect],
    data: { title: 'forgot_password' },
  },
  {
    path: 'reset-password/:token',
    loadChildren: () => import('./auth/reset-password/reset-password.module').then(m => m.ResetPasswordModule),
    canActivate: [LoginRedirect],
    data: { title: 'reset_password' },
  },
  {
    path: 'link-expired',
    loadChildren: () => import('./auth/message/message.module').then(m => m.MessageModule),
    canActivate: [LoginRedirect],
    data: { title: 'link_expired' },
  },
  {
    path: 'company',
    loadChildren: () => import('./company/company-list/company-list.module').then(m => m.CompanyListModule),
    canActivate: [EnsureAuthenticated, CompanyRedirectService],
    data: { title: 'company_list' },
  },
  {
    path: 'dashboard',
    loadChildren: () => import('./dashbord/dashbord.module').then(m => m.DashbordModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'brand',
    loadChildren: () => import('./brand/brand-list/brand-list.module').then(m => m.BrandListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'category',
    loadChildren: () => import('./category/category-list/category-list.module').then(m => m.CategoryListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'product',
    loadChildren: () => import('./product/product-list/product-list.module').then(m => m.ProductListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'search',
    loadChildren: () => import('./user-search/user-search-list/user-search-list.module').then(m => m.UserSearchListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'bankinfo',
    loadChildren: () => import('./user-bankinfo/user-bankinfo-list/user-bankinfo-list.module').then(m => m.UserBankinfoListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'banner',
    loadChildren: () => import('./banner/banner-list/banner-list.module').then(m => m.BannerListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'cart',
    loadChildren: () => import('./cart/cart-list/cart-list.module').then(m => m.CartListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'customer-review',
    loadChildren: () => import('./customer-review/customer-review-list/customer-review-list.module').then(m => m.CustomerReviewListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'feedback',
    loadChildren: () => import('./feedback/feedback-list/feedback-list.module').then(m => m.FeedbackListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'offer-product',
    loadChildren: () => import('./product/offer-product-list/offer-product-list.module').then(m => m.OfferProductListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'payment-details',
    loadChildren: () => import('./payment-details/payment-details-list/payment-details-list.module').then(m => m.PaymentDetailsListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'subscribe',
    loadChildren: () => import('./subscribe/subscribe-list/subscribe-list.module').then(m => m.SubscribeListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'favourite',
    loadChildren: () => import('./user-favourite/user-favourite-list/user-favourite-list.module').then(m => m.UserFavouriteListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
   {
    path: 'wishlist',
    loadChildren: () => import('./user-favourite/wishlist-list/wishlist-list.module').then(m => m.WishlistListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },
  {
    path: 'offer',
    loadChildren: () => import('./offers/offers-list/offers-list.module').then(m => m.OffersListModule),
    canActivate: [EnsureAuthenticated, IsCompanySelected],
    data: { title: 'dashboard' },
  },

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
